<template>
  <div id="app">
    <h1>我的待办事项列表</h1>
    <to-do-form @todo-added="addToDo"></to-do-form>
    <el-divider></el-divider>
    <el-list>
      <el-list-item v-for="item in ToDoItems" :key="item.id">
        <to-do-item
          :label="item.label"
          :done="item.done"
          :id="item.id"
          @checkbox-changed="updateDoneStatus(item.id)"
          @item-deleted="deleteToDo(item.id)"
          @item-edited="editToDo(item.id, $event)">
        </to-do-item>
      </el-list-item>
    </el-list>
    <div ref="chart" style="width: 600px;height:400px;"></div>
  </div>
</template>

<script>
import ToDoItem from "./components/ToDoItem.vue";
import uniqueId from "lodash.uniqueid";
import ToDoForm from "./components/ToDoForm";
import * as echarts from 'echarts'
export default {
  name: "app",
  components: {
    ToDoItem,
    ToDoForm,
  },
  data() {
    return {
      ToDoItems: [
        { id: uniqueId("todo-"), label: "学习VUE", done: false },
        {
          id: uniqueId("todo-"),
          label: "使用Vue CLI创建Vue项目",
          done: true,
        },
        { id: uniqueId("todo-"), label: "玩得开心", done: true },
        { id: uniqueId("todo-"), label: "创建办事项列表", done: false },
      ],
      chart: null, // 用于存储ECharts实例
    };
  },
  mounted() {
    this.initChart();
    this.updateChart();
  },
  methods: {
    addToDo(toDoLabel) {
      this.ToDoItems.push({id:uniqueId('todo-'), label: toDoLabel, done: false});
      this.updateChart();
    },
    updateDoneStatus(toDoId) {
      const toDoToUpdate = this.ToDoItems.find((item) => item.id === toDoId)
      toDoToUpdate.done = !toDoToUpdate.done
      this.updateChart();
    },
    deleteToDo(toDoId) {
      const itemIndex = this.ToDoItems.findIndex((item) => item.id === toDoId);
      this.ToDoItems.splice(itemIndex, 1);
      this.$refs.listSummary.focus();
      this.updateChart();
    },
    editToDo(toDoId, newLabel) {
      const toDoToEdit = this.ToDoItems.find((item) => item.id === toDoId);
      toDoToEdit.label = newLabel;
    },
    initChart() {
      const chartDom = this.$refs.chart;
      this.chart = echarts.init(chartDom);
      this.chart.setOption(this.getOptions());
    },
    getOptions() {
      const completed = this.ToDoItems.filter(item => item.done).length;
      const total = this.ToDoItems.length;
      return {
        series: [
          {
            name: '待办事项',
            type: 'pie',
            data: [
              { value: completed, name: '已完成' },
              { value: total - completed, name: '未完成' }
            ]
          }
        ]
      };
    },
    updateChart() {
      if (this.chart) {
        this.chart.setOption(this.getOptions());
      }
    },
  },
  computed: {
    listSummary() {
      const numberFinishedItems = this.ToDoItems.filter((item) =>item.done).length
      return `${numberFinishedItems} out of ${this.ToDoItems.length} items completed`
    }
  }

};

</script>

